import React, { Component } from 'react';
import MIDISounds from 'midi-sounds-react';
import './App.css';

var d = new Date();
const keyOfCmaj = {"I":"Cmaj","ii":"Dmin","iii":"Emin","IV":"Fmaj","V":"Gmaj","vi":"Amin","vii":"Bm7♭5"}
const keyOfAmin = {"i":"Amin","ii":"Bm7♭5","III":"Cmaj","iv":"Dmin","v":"Emin","VI":"Fmaj","VII":"Gmaj"}


/*
const majorDirection =
  {
   "I":["ii","iii","IV","V","vi","vii"],
   "ii":["vii","V"],
   "iii":["vi","IV"],
   "IV":["I","ii","V"],
   "V":["I","vi"],
   "vi":["IV","ii"],
   "vii":["I"]
 }
 */

const majorDirection =
  {
   "I":["ii","iii","IV","V","vi",],
   "ii":["V"],
   "iii":["vi","IV"],
   "IV":["I","ii","V"],
   "V":["I","vi"],
   "vi":["IV","ii"],
 }

 const minorDirection =
 {
   "i":["ii","III","iv","v","VI","VII"],
   "ii":["v","VII"],
   "III":["iv","VI"],
   "iv":["ii","v","VII","i"],
   "v":["i","VI"],
   "VI":["iv","ii"],
   "VII":["III"],
 }

 const notes2Midi = {
   "Cmaj":[48,52,55],
   "Dmin":[50,53,57],
   "Emin":[52,55,59],
   "Fmaj":[53,57,48],
   "Gmaj":[55,59,50],
   "Amin":[57,48,52],
   "Bm7♭5":[59,50,53]
 }



class App extends Component {
  constructor(props){
    super(props);

    this.generateChordProgression = this.generateChordProgression.bind(this)
    this.newCotd = this.newCotd.bind(this)
    this.playSound = this.playSound.bind(this)
    this.toggleRandom = this.toggleRandom.bind(this)
    this.state = {isRandom:false}
    var cotd = this.generateChordProgression();
    this.state = {isRandom:false, cotd:cotd}
  }

  newCotd(){
    let progLen = 4;
    if(this.refs.progressionLength.value !== (null||undefined)){
      progLen = this.refs.progressionLength.value;
    }
    let newCotd = this.generateChordProgression(Number(progLen));
    this.setState({cotd:newCotd})
  }
  toggleRandom(){
    this.setState({isRandom:!this.state.isRandom})
    console.log('state is now',this.state)
  }
  playSound(chordArray){
    if(this.refs.midi){
      this.refs.midi.playChordNow(3,chordArray,1)
    }
  }
  generateChordProgression(progLength=4){
    let prog = [];
    let startChord, nextChord, keyChords, direction, majOrMin;

    if(this.refs.aminor && this.refs.cmajor && this.refs.either){

      if (this.refs.aminor.checked === true){
        majOrMin = 1;
      }

      if (this.refs.cmajor.checked === true){
        majOrMin = 0;
      }

      if(this.refs.either.checked === true){
        majOrMin = Math.floor(Math.random()*2);
      }
    }else{
      majOrMin = Math.floor(Math.random()*2);
    }


      if(majOrMin === 0){
        //major
        startChord="I"
        keyChords = keyOfCmaj;
        direction = majorDirection
      }else{
        //minor
        startChord="i"
        keyChords = keyOfAmin;
        direction = minorDirection
      }



      //use this loop to produce random length progressions that follow the rules and resolve back to i/I
    if(this.state.isRandom === true){
      while(nextChord !== startChord){
          if(prog.length===0){nextChord=startChord}
          prog.push(nextChord)
          let rand = Math.floor(Math.random()*direction[nextChord].length)
          nextChord = direction[nextChord][rand]
      }
      prog.push(startChord)
  }else{

      //use this loop to produce progressions of a spefic length that don't necessarily resolve back to i/I
      while(prog.length !== progLength){
          if(prog.length===0){nextChord=startChord}
          prog.push(nextChord)
          let rand = Math.floor(Math.random()*direction[nextChord].length)
          nextChord = direction[nextChord][rand]
      }
    }
      //optionally add the final i/I chord to the progression
      //prog.push(startChord)

    let chords = prog.map(chord=>keyChords[chord]);

    return {"_type":(majOrMin===0)?"major":"minor","key":(majOrMin===0)?"C Major":"A Minor", length:prog.length,"roman":prog.join(" - "),"chords":chords.join(" - ")}

  }
  render() {
    return (
      <div className="App">
        <MIDISounds className="hidden" ref='midi' appElementName="root" instruments={[3]} />
        <header className="App-header">
        <div className='headerbg'>
          <h4>Simple Chord Progression Generator</h4>
          </div>
          <p>
          {this.state.cotd.length} chords in the key of {this.state.cotd.key}:<br/>
          <span className="chords">{this.state.cotd.roman}</span><br/>
          <span>{this.state.cotd.chords.split(" - ").map(chord=>{
            return <span key={d.getTime()*Math.random()*50}><button className='chordbtn' key={d.getTime()*Math.random()*50} onClick={() => this.playSound(notes2Midi[chord])}>{chord} &#9658;</button> </span>;
          })}</span>
          </p>
          <button className='newbtn'
            onClick={this.newCotd}
          >
            Generate New Progression
            </button>
            <br className='hh'/>
          <div className="appBody">
          <h4 className='subhead'>Options</h4>
            <p className="options">
              <span className={(this.state.isRandom)?'greystrike':'orange'}><strong>length</strong></span> <select ref='progressionLength' disabled={this.state.isRandom}>
                <option value="4">4</option>
                <option value="5">5</option>
                <option value="6">6</option>
                <option value="7">7</option>
                <option value="8">8</option>
                <option value="9">9</option>
                <option value="10">10</option>
                <option value="11">11</option>
                <option value="12">12</option>
              </select> &nbsp; <span className='white'><strong><u>OR</u></strong></span> &nbsp;  <strong>random length<sup>*</sup></strong> <input type="checkbox" onClick={this.toggleRandom} ref='isRandom'/></p>
                <p className='options'><strong><input type='radio' id='major' ref='cmajor' name='mkey'/>C major &nbsp; <input type='radio' id="minor" ref='aminor' name='mkey'/>A minor &nbsp; <input type='radio' ref='either' id='either' name='mkey' defaultChecked/>either</strong></p>
                <p className='tiny'><strong>*</strong> the random length option attempts to resolve to the i/I chord.</p>
            <br/>
            </div>
            <h5>&copy; 2019 Bill Pickard. All Rights Reserved.<br/>Version 1.0</h5>

        </header>

      </div>
    );
  }
}

export default App;
